export default {
	data() {
		return {
			active: true,
			anime:true
		}
	},
	methods: {
		openInfo() {
			this.active = true
			this.anime = true
		},
		closeInfo(){
			this.anime = false
		},
		animeEnd(){
			if(!this.anime){
				this.active = false
			}
		}
	}
}
